import React from 'react'
import ErrorBoundary from './common/ErrorBoundary'
import Routing from './common/Routing'
import { ROUTES } from './routes'

const basename = document.location.host.indexOf('github.io') >= 0 ? '/griffeath-machine' : '/'

const App = () => <ErrorBoundary><Routing routes={ROUTES} basename={basename} /></ErrorBoundary>

export default App
